import React from 'react'
import './style.css'

const Header = () => (
  <div className="like-detail-header">
    <span className="back" onClick={() => window.history.go(-1)}>
      <i className="icon-chevron-left"></i>
      
    </span>
    <span className="title"></span>
  </div>
)

export default Header



