import React from 'react'
import { Link } from 'react-router-dom'
import './style.css'

const GroupList = ({cls, data, cPrice, oPrice}) => (
  <table className={cls}>
    {
      data.map((item, index) => (
        <tbody key={index}>
          <ThItem title={item.title} />
          {
            item.list.map((td, i) => (
              <TdItem key={i} td={td}/>
            ))
          }
        </tbody>
      ))
    }
    <FootItem cPrice={cPrice} oPrice={oPrice}/>
  </table>
)

const ThItem = ({title}) => (
  <tr>
    <th colSpan={3}>{title}</th>
  </tr>
)

const TdItem = ({td}) => (
  <tr>
    <td>{td.name}</td>
    <td>{td.num}</td>
    <td>{td.price}</td>
  </tr>
)

const FootItem = ({oPrice, cPrice}) => (
  <tbody className="foot">
    <tr>
      <td></td>
      <td className="high-price"></td>
      <td className="high-price">{oPrice}</td>
    </tr>
    <tr>
      <td style={{color: '#999'}}></td>
      <td className="tuan-price"><strong></strong></td>
      <td className="tuan-price"><strong>{cPrice}</strong></td>
    </tr>
  </tbody>
)

const GroupDetail = ({groupBuyDetail, oPrice, cPrice, id}) => {
  return (
    <div className="like-detail-group">
      <h3 className="title"><i></i></h3>
      <GroupList cls="tab" data={groupBuyDetail} oPrice={oPrice} cPrice={cPrice}/>
      <Link to={`/submit/${id}/${cPrice}`} className='btn'></Link>
    </div>
  )
}

export default GroupDetail



